olorsName}-#{$i} {\n      color: nth($colors, $i);\n      // Edge: High contrast mode\n      @media screen and (-ms-high-contrast: active) {\n        color: windowText;\n        [data-inverts-on-hover=\"high-contrast\"]:hover &, [data-inverted=\"high-contrast\"] & {\n          color: window;\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
